.App {
  display: flex;
  flex-direction: column;
  color: black;
  background-color: white;
}

.App.night-mode {
  background-color: black;
  color: #00FF00;
}

.line-and-ball-container {
  flex: 1; /* Faz com que este componente ocupe o máximo de espaço disponível */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body.night-mode {
  background-color: black;
  color: #00FF00;
}
